import ChangePassword from './forms/ChangePassword'
import ChangeMail from './forms/ChangeMail'
import ChangeTel from './forms/ChangeTel'
import ChangeFio from './forms/ChangeFio'
import DeleteLS from './forms/DeleteLS'
import DeleteProfile from './forms/DeleteProfile'
import ProfileSubscriptions from './forms/ProfileSubscriptions'

import { mapGetters } from 'vuex'
export default {
  name: 'UserProfile',
  data: ()=> ({
    telVerif: '',
    mailVerif: ''
  }),
  computed: mapGetters(['userAccountsNumbers', 'userVerificationTelAnsver', 'userVerificationMailAnsver']),
  async mounted(){
    await this.$store.dispatch('userAccountsFetch')
    this.telVerif = this.userVerificationTelAnsver
    this.mailVerif = this.userVerificationMailAnsver
    localStorage.setItem('weltumLastUrl', window.location.pathname)
  },
  components: {
    ChangeMail, ChangePassword, DeleteProfile, 
     ChangeFio, DeleteLS, ChangeTel, ProfileSubscriptions
  } 
}
</script>
