<template>
  <b-col 
    class="px-4 mt-3 mx-auto" 
    cols="12" md="10" lg='8'
  >
    <form 
      class="change-mail-form p-2 m-2" 
      @submit.prevent="changeEmail" 
      novalidate
    >
      <h4 class="mb-3 form-header">{{'userProfileChangeFIO' | localize}}:</h4>
      <div class="new-address-description ">{{'userProfileChangeFIONewData' | localize}}:</div>
      <input 
        v-model="new_fio"
        type="text"
        class="form-control"
      >
      <div 
          class="form-description"
          v-if="!$v.new_fio.$dirty && !$v.new_fio.required"
      >
          {{'userProfileChangeFIONewDescription' | localize}}
      </div>
      <div 
          class="form-description-error"
          v-if="$v.new_fio.$dirty && !$v.new_fio.required"
      >
          {{'newAddressRequiredInput' | localize}}
      </div>
      <div class="new-address-description ">{{'userProfileChengeCurrentPass' | localize}}:</div>
      <input 
          type="password" 
          class="form-control"
          v-model="user_pass"
      >
      <div 
          class="form-description-error"
          v-if="$v.user_pass.$dirty && !$v.user_pass.required"
      >
          {{'newAddressRequiredInput' | localize}}
      </div> 
      <b-button 
        class="my-3 style-button button-profile"
        type="submit"
        variant="outline-danger"
      >
        {{'userProfileButton' | localize}} <div class="style-button-icon"> <b-icon  icon="person" ></b-icon> </div>
      </b-button>
    </form>

  </b-col>  
</template>
<script>
import base64url from "base64url";
import { mapGetters } from 'vuex'
import { validationMixin } from 'vuelidate'
import { required } from 'vuelidate/lib/validators'
export default {
  mixins: [validationMixin],
  data: ()=> ({
    new_fio: '',
    user_pass: '',
  }),
  validations: {
      new_fio: {required},
      user_pass: {required},
  },
  mounted(){
    //console.log(this.userInfoData.items[0], 'test user data ')
    this.new_fio = localStorage.weltum_user_fio
  },
  computed: mapGetters(['userInfo_data', 'locale']),
  methods: {
    async changeEmail(){
      this.changeMail = true
      if(this.$v.$invalid){
        //console.log(this.$v)
        this.$v.$touch()
      } else {
        const PASS = base64url.decode(localStorage.weltumPass ) 
        if(this.user_pass === PASS) {
          const user = {
              login: localStorage.weltumAuth,
              password: PASS,
              fio: this.new_fio,
              tel: localStorage.weltum_user_tel, 
              mail: localStorage.weltum_user_mail,
              secret_key: localStorage.weltumKey
          }
          await this.$store.dispatch('changeUserData', user )
          if(this.userInfo_data.msg === 'OK'){
            this.$messageLocalized('dataChanged')
            localStorage.setItem('weltum_user_fio', this.new_fio) 
            this.user_pass = '';
          }
        } else if (this.user_pass !== PASS) {
          this.$messageLocalized('wrongPassword')
        }

      }       
    },
  }
  
}
</script>
