import axios from 'axios'
import { sha256 } from 'js-sha256';
import base64url from "base64url";
import config from '@/config'

const API_URL = config.API_URL

export default {
  state: {
    response_message: ''
  },
  mutations: {
    addBillResponse: (state, response) => state.response_message = response,
  },
  actions: {
    async addNewAddress(ctx, user){
      const key = localStorage.getItem("weltumKey")
      const Time = Date.now().toString()
      const test = sha256.hmac(key, Time);
      const Hmac = base64url(test)
      //console.log(Time, Hmac)
      await axios.post(
        `${API_URL}/api/rest/receipt/kabinet_new_sch`,
        user, 
        {
          headers:{
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'X-Hmac': Hmac,
          'X-Time': Time, 
          }
        },
        
      ).then((response) => {
        //console.log(response.data.msg)
        localStorage.setItem('weltumTimeAuth', Date.now())
        ctx.commit('addBillResponse', response.data.msg)
      })
    }
  },
  getters:{
    responseMessage: s => s.response_message,
  },
}