import axios from 'axios'
import { sha256 } from 'js-sha256';
import base64url from "base64url";
import config from '@/config'

const API_URL = config.API_URL

export default {
  state: {
    delete_ls_data: []
  },
  mutations: {
    deleteLsMutation: (state, response) => state.delete_ls_data = response,
  },
  actions: {
    async deleteLS(ctx, LS){
      const key = localStorage.getItem("weltumKey")
      const Time = Date.now().toString()
      const test = sha256.hmac(key, Time);
      const Hmac = base64url(test)
      // console.log(Time, Hmac)
      await axios.delete(
        `${API_URL}/api/rest/receipt/delete_schet?secret_key=${key}&lico=${LS}`, 
        {
          headers:{
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'X-Hmac': Hmac,
          'X-Time': Time, 
          }
        },
        
      ).then((response) => {
        // console.log(response.data, 'удаление ЛС')
        ctx.commit('deleteLsMutation', response.data)
        localStorage.setItem('weltumTimeAuth', Date.now())
      })
    }
  },
  getters:{
    delete_ls_data: s => s.delete_ls_data,
  },
}