import axios from 'axios'
import config from '@/config'

const API_URL = config.API_URL

export default {
  state: {
    user_Info_data: [],
    registration_response: null
  },
  mutations: {
    responseFetch: (state, response) => state.registration_response = response,
  },
  actions: {
    async registrationUser(ctx, user){
      await axios.post(
        `${API_URL}/api/rest/receipt/kabinet_in_ident`,
        user,
        {
          headers:{
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          }
        },
        
      ).then((response) => {
        // console.log(response.data)
        ctx.commit('responseFetch', response.data)
      })
    }
  },
  getters:{
    registrationResponse: s => s.registration_response,
  },
}