import axios from 'axios'
import { sha256 } from 'js-sha256';
import base64url from "base64url";
import config from '@/config'

const API_URL = config.API_URL

export default {
  state: {},
  mutations: {},
  actions: {
    async deleteProfile(){
      const key = localStorage.getItem("weltumKey")
      const Time = Date.now().toString()
      const test = sha256.hmac(key, Time);
      const Hmac = base64url(test)
      // console.log(Time, Hmac)
      await axios.delete(
        `${API_URL}/api/rest/receipt/delete_kab?secret_key=${key}`, 
        {
          headers:{
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'X-Hmac': Hmac,
          'X-Time': Time, 
          }
        },
        
      ).then((response) => {
        // console.log(response.data, 'удаление кабинета')
          if(response.data.msg === 'OK'){
            this.$messageLocalized("profileDeleted")
          }
        localStorage.setItem('weltumTimeAuth', Date.now())
      })
    }
  },
  getters:{},
}