<script>
import config from '@/config'
import MaskedInput from 'vue-masked-input'
import VueRecaptcha from 'vue-recaptcha';
import { validationMixin } from 'vuelidate'
import { required, minLength, maxLength, email, helpers, sameAs } from 'vuelidate/lib/validators'
const alpha = helpers.regex('alpha', /^[a-zA-Zа-яА-яёЁїЇЄєґҐіІ`'’ -]+$/)
export default {
    mixins: [validationMixin],
    data: () => ({
        passwordFieldType: "password",
        passwordButtonState: 'hide_password_button',
        captcha_key: config.CAPTCHA_KEY,
        captcha_verify: null,
        consumer_name: '',
        legal_address: '',
        ugodaUrl: config.API_URL + '/api/rest/receipt/get_ugoda',
        user_INN: '',
        acessCode: 123,
        userName: '',
        userEmail: '',
        userPhone: '',
        userLogin: '',
        userPassword: '',
        confirmUserPassword: '',
    }),
    validations: {
        user_INN: {required, minLength: minLength(6), maxLength: maxLength(10),},
        acessCode: {required},
        userName: {required, minLength: minLength(10), alpha},
        userEmail: { required, email },
        userPhone: { required,},
        userLogin: { minLength: minLength(8) },
        userPassword: {required, minLength: minLength(6), maxLength: maxLength(16),},
        confirmUserPassword: {sameAs: sameAs('userPassword')},
    },
    methods: {
// методы каптчи
        onSubmit: function () {
        this.$refs.invisibleRecaptcha.execute()
        },
        onVerify: function (response) {
            this.captcha_verify = response
        },
        onExpired: function () {
            //console.log('Expired')
        },
        resetRecaptcha () {
            this.$refs.recaptcha.reset()
        },
// методы формы
        INNsearch(){
            this.user_INN = 123456789
        },
        acessCodeReview(){
            this.acessCode == 123 ? this.consumer_name = this.legal_address = 'test' : this.consumer_name = this.legal_address = ''
        },
        sendAuthLegalUser(){ !this.captcha_verify ? console.log('captcha error') : this.legalEntityData() },
        legalEntityData(){
          if(this.$v.$invalid){
            // console.log(this.$v)
            this.$v.$touch()
            // return
/*          } else {
            const user = {
              user_INN: this.user_INN,
              userAcessCode: this.acessCode,
              consumer_name: this.consumer_name,
              user_legal_address: this.legal_address,
              userName: this.userName,
              userEmail: this.userEmail,
              userPhone: this.userPhone,
              userLogin: this.userLogin,
              userPass: this.userPassword,
              recaptchaToken: this.captcha_verify
            }
            //console.log(user)*/
          }
        },
        showPassword(){
          if(this.passwordFieldType === 'password') {
              this.passwordFieldType = 'text'
              this.passwordButtonState = 'show_password_button'
           } else {
              this.passwordFieldType = 'password'
              this.passwordButtonState = 'hide_password_button'
           }
        }
    },
    components: {
        MaskedInput, VueRecaptcha
    }
}
</script>
