<template>

    <form 
      class="change-mail-form p-2" 
      @submit.prevent="changeTel" 
      novalidate
    >
      <div class="new-address-description ">{{'userProfileChangeTelNewTel' | localize}}:</div>
      <masked-input 
          v-model="new_tel"  
          mask="\+\38 (111) 111-11-11" 
          class="form-control" 
          type="tel"
          :placeholder="phone_mask"
      />

      <div 
          class="form-description"
          v-if="!$v.new_tel.$dirty && !$v.new_tel.required"
      >
         {{'registrationUserDataTelephoneError' | localize}}
      </div>
      <div class="new-address-description ">{{'userProfileChengeCurrentPass' | localize}}:</div>
      <input 
          type="password" 
          class="form-control"
          v-model="user_pass"
          
      >
      <div 
          class="form-description-error"
          v-if="$v.user_pass.$dirty && !$v.user_pass.required"
      >
          {{'newAddressRequiredInput' | localize}}
      </div> 
      <b-button 
        class="my-3 style-button button-profile"
        type="submit"
        variant="outline-danger"
      >
        {{'userProfileButton' | localize}} <div class="style-button-icon"> <b-icon  icon="telephone" ></b-icon> </div>
      </b-button>
    </form>


</template>
<script>
import base64url from "base64url";
import { mapGetters } from 'vuex'
import MaskedInput from 'vue-masked-input'
import { validationMixin } from 'vuelidate'
import { required, minLength } from 'vuelidate/lib/validators'
export default {
  mixins: [validationMixin],
  data: ()=> ({
    tel: '',
    new_tel: '',
    user_pass: '',
    phone_mask: ''
  }),
  props: {
    editData: {
      type: Function,
    }
  },
  validations: {
      new_tel: {required, },
      user_pass: {required},
      tel: {minLength:minLength(12)}
  },
  mounted(){
    this.tel  = localStorage.weltum_user_tel
    this.phoneMask()
    this.new_tel = this.phone_mask
  },
  computed: mapGetters(['userInfo_data', 'locale']),
  methods: {
    phoneMask(){
      this.phone_mask = '+38 ' + '(' + this.tel[2] + this.tel[3] + this.tel[4] + ') ' + this.tel[5] + this.tel[6] + this.tel[7] + '-' + this.tel[8] + this.tel[9] + '-' + this.tel[10] + this.tel[11]
    },
    async changeTel(){
      if(this.new_tel.replace(/\D/g,'').length >= 12) {
        this.changeMail = true
        if(this.$v.$invalid){
          //console.log(this.$v)
          this.$v.$touch()
        } else {
          const PASS = base64url.decode(localStorage.weltumPass )
          let tel = this.new_tel.replace(/\D/g,'');
          if(this.user_pass === PASS){
            const user = {
                login: localStorage.weltumAuth,
                password: PASS,
                fio: localStorage.weltum_user_fio,
                tel: tel, 
                mail: localStorage.weltum_user_mail,
                secret_key: localStorage.weltumKey,
            }
            await this.$store.dispatch('changeUserData', user )
            if(this.userInfo_data.msg === 'OK'){
              this.$messageLocalized('dataChanged')
                localStorage.setItem('weltum_user_tel', tel) 
                this.user_pass = '';
                this.$emit('editData', false, tel)
            }
          } else if (this.user_pass !== PASS) {
            this.$messageLocalized('wrongPassword')
          }

        }      
      }
    },
  },
  components: {MaskedInput}
  
}
</script>
