<script>
import { mapGetters } from 'vuex'
import loader from '../Loader'
// import PaymentDialog from "./PaymentDialog";
import config from '@/config'
import axios from 'axios'
import { sha256 } from 'js-sha256';
import base64url from "base64url";
export default {
  data: ()=>({
    loader: false,
    user_account_residents: [],
    user_id: null,
    answers_response: null,
    pay: 0,
    account: window.location.pathname.split('/').pop(),
    route_prefix: config.ROUTE_PREFIX,
    tarif: null
  }),
  async mounted() {
    this.loader = true
    this.user_id = window.location.pathname.split('/').pop()
    await this.$store.dispatch('userAccountsInfoData')
        if(this.userAccountsinfo.err === "Сессия просрочена !"){
      await this.$store.dispatch('logout')
      this.$router.push({  name: 'Login' })
      this.$toastLocalized("sessionTerminated")
    }
    await this.$store.dispatch('userAccountsBillInfoData')
    await this.$store.dispatch('userAccountSubsidyInfo')
    await this.$store.dispatch('fetchAnswers')
    this.tarifInfo()
    this.answers_response = this.answersData
    this.user_account_residents = this.userAccountResidentsData
    // console.log(this.userAccountsinfo, this.answers_response ,"123инфо о юзере")
    
    this.loader = false
    this.pay = this.userAccountsinfo.balance_today

  },
  computed: mapGetters(['userAccountsinfo','userAccountResidentsData', 'answersData', 'locale']),
  methods: {
    async tarifInfo(){
      const key = localStorage.getItem("weltumKey")
      const Time = Date.now().toString()
      const test = sha256.hmac(key, Time);
      const Hmac = base64url(test)
      const API_URL = config.API_URL

      await axios.get(
        `${API_URL}/api/rest/receipt/tarif?lico=${this.user_id}&secret_key=${key}`, 
        {
          headers:{
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'X-Hmac': Hmac,
          'X-Time': Time, 
          }
        },
        
      ).then((response) => {
        
        this.tarif = response.data.items[0]
        // console.log(this.tarif, 'тариф инфо')
      })
    },
    showModal() {
      this.$refs['bv-modal-example'].show()
    },
    hideModal() {
      this.$refs['bv-modal-example'].hide()
    },
    async deleteMessage(messageId){
      const message = {
        id: messageId,
        secret_key: localStorage.weltumKey
      }
      // console.log(message, this.answers_response)
      await this.$store.dispatch('deleteMessageAction', message)
      this.answers_response.forEach( i => {
        if (i.id === message.id) {this.answers_response.splice(i, 1)}
      })
      this.$messageLocalized("messageDeleted")
    },
    async portmonePayMethod(){
      await this.$store.dispatch('portmonePay')
    },
    async privatPayMethod(){
      await this.$store.dispatch('privatPay')
    },
    async zssPayMethod(){
      await this.$store.dispatch('ZssPay')
    },
    zsslink: function (account_number){
      return `${config.ZSS_PAY_URL}${account_number}`
    }
  },
  components: {
    loader,
    // PaymentDialog
  }
}
</script>
