</template>
<script>
import { mapGetters } from 'vuex'
import Header from '../components/Header'
import loader from '../components/Loader'
import ConfirmMail from '../components/userProfile/forms/ConfirmMail'
import ConfirmPhone from '../components/userProfile/forms/ConfirmPhone'
// import PaymentDialog from "../components/userAddress/PaymentDialog";
import config from '@/config'
import {sha256} from "js-sha256";
import base64url from "base64url";
import axios from "axios";
import langFilter from "../filters/langFilter";

const ROUTE_PREFIX = config.ROUTE_PREFIX
const API_URL = config.API_URL
export default {
  data: ()=>({
    modal1: false,
    loader: false,
    user_more_info: false,
    account_numbers: [],
    account_data: {},
    verificationMail: null,
    verificationTel: null,
    route_prefix: ROUTE_PREFIX,
    public_info:{},
    modal1_mail_confirm: false,
    modal1_phone_confirm: false
  }),
  methods: {
    language: function (key) {
      return langFilter(key)
    },
    zsslink: function (account_number){
      return `${config.ZSS_PAY_URL}${account_number}`
    }
  },
  mounted: async function () {
    this.loader = true
    localStorage.setItem('weltumLastUrl', window.location.pathname)
    await this.$store.dispatch('userAccountsFetch')
    // console.log(this.userAccountsNumbers)

    await this.$store.dispatch('verification', 1)
    this.verificationMail = this.userVerificationMailAnsver
    if (this.verificationMail === 'Почтовый адрес не подтвержден !') {
      this.modal1_mail_confirm = true
    }
    await this.$store.dispatch('verification', 2)
    this.verificationTel = this.userVerificationTelAnsver
    if (this.verificationTel === 'Телефон не подтвержден !') {
      this.modal1_phone_confirm = true
    }

    this.account_numbers = this.userAccountsNumbers
    if (this.account_numbers.length && this.account_numbers[0].err === "Сессия просрочена !") {
      await this.$store.dispatch('logout')
      this.$router.push({path: `${ROUTE_PREFIX}/login`})
      this.$toastLocalized("sessionTerminated")
    }

    const key = localStorage.getItem("weltumKey")
    const Time = Date.now().toString()
    const test = sha256.hmac(key, Time);
    const Hmac = base64url(test)
    let promises = []
    let items = {}
    let info = {}
    this.account_numbers.map(async function(item) {
      promises.push(
          axios.get(
              `${API_URL}/api/rest/receipt/account_addres_info?lico=${item.account_id}&secret_key=${key}`,
              {
                headers: {
                  'Content-Type': 'application/json',
                  'Accept': 'application/json',
                  'X-Hmac': Hmac,
                  'X-Time': Time,
                }
              },
          ).then((response) => {
            item = response.data.items[0]
            if(item.balance_today < 0.0){
              item.bill_status = 'plus'
            }else if(item.balance_today > 0.0){
              item.bill_status = 'minus'
            }else{
              item.bill_status = 'zero'
            }
            // items[response.data.items[0].account_id.toString()] = item
            items[item.account_id.toString()] = item
          })
      )
    });
    promises.push(
        axios.get(
            `${API_URL}/api/rest/receipt/info_predpr?tema=0&secret_key=${key}`,
            {
              headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'X-Hmac': Hmac,
                'X-Time': Time,
              }
            },
        ).then((response) => {
          info = response.data
        })
    )
    Promise.all(promises).then(() => {
      this.account_data = items
      this.public_info = info
      this.loader = false

    });
  },
  computed: mapGetters(['userAccountsNumbers',  'locale', 'userVerificationMailAnsver', 'userVerificationTelAnsver']),
    components: {
      // PaymentDialog,
      Header,
      loader,
      ConfirmMail,
      ConfirmPhone
    }
}
</script>