<template>
  <b-col 
    class="px-4 mt-3 mx-auto" 
    cols="12" md="10" lg='8'
  >
    <form 
      class="change-mail-form p-2 m-2" 
      @submit.prevent="changePasword" 
      novalidate
    >
      <h4 class="mb-3 form-header">{{'userProfileChengePassHeader' | localize}}:</h4>
      <!-- <div class="new-address-description ">{{'userProfileChengeCurrentPass' | localize}}:</div>
      <input 
          type="password" 
          class="form-control"
          v-model="user_password"
      >
      <div 
          class="form-description-error"
          v-if="$v.user_password.$dirty && !$v.user_password.required"
      >
          {{'newAddressRequiredInput' | localize}}
      </div>  -->
      <div class="new-address-description ">{{'userProfileChengeNewPass' | localize}}:</div>
      <input 
          type="password" 
          class="form-control"
          v-model="new_user_password"
      >
      <div 
          class="form-description-error"
          v-if="$v.new_user_password.$dirty && !$v.new_user_password.required"
      >
          {{'newAddressRequiredInput' | localize}}
      </div> 
      <div 
          class="form-description-error"
          v-if="$v.new_user_password.$dirty && !$v.new_user_password.minLength"
      >
          {{'userProfileMinSymbols' | localize}}
      </div>
      <div class="new-address-description ">{{'userProfileAcessPass' | localize}}:</div>
      <input 
          type="password" 
          class="form-control"
          v-model="confirm_new_user_password"
      >
      <div 
          class="form-description-error"
          v-if="$v.confirm_new_user_password.$dirty && !$v.confirm_new_user_password.sameAs"
      >
          {{'userProfilePassCorrect' | localize}}
      </div>
      <b-button 
        class="mt-4 mb-3 style-button button-profile-width"
        type="submit"
        variant="outline-danger"
      >
        {{'userProfileChangePassButton' | localize}} 
        <div class="style-button-icon"> 
          <b-icon  icon="person" ></b-icon> 
        </div> 
      </b-button>
    </form>
  </b-col>  
</template>
<script>
import base64url from "base64url";
import { mapGetters } from 'vuex'
import { validationMixin } from 'vuelidate'
import { required, sameAs, minLength } from 'vuelidate/lib/validators'
export default {
  mixins: [validationMixin],
  data: ()=> ({
    new_user_password: '',
    confirm_new_user_password: '',
  }),
  validations: {
      new_user_password: {required, minLength: minLength(8)},
      confirm_new_user_password: {sameAs: sameAs('new_user_password')},
  },
  computed: mapGetters(['userInfoData', 'locale']),
  methods: {
    async changePasword(){
      if(this.$v.$invalid){
        //console.log(this.$v)
        this.$v.$touch()
     } else {
        const user = {
            login: localStorage.weltumAuth,
            password: this.new_user_password,
            fio: localStorage.weltum_user_fio,
            tel: localStorage.weltum_user_tel, 
            mail: localStorage.weltum_user_mail,
            secret_key: localStorage.weltumKey
        }
        // console.log(user)
        await this.$store.dispatch('changeUserData', user )
        if(this.userInfoData.msg === 'OK'){
          this.$messageLocalized('dataChanged')
          localStorage.setItem('weltumPass', base64url(this.new_user_password) )
          this.new_user_password = this.confirm_new_user_password = ''
            
        }
      }
    }
  }

}
</script>