<template>

  <div>
    <b-modal
      :ok-disabled="disabled_button"
      ok-only
      hide-header-close
      no-close-on-backdrop
      no-close-on-esc
      no-enforce-focus
      id="modal-prevent-closing"
      v-model="test"
      @cancel="exit"
      ok-variant="danger"
    >
      <template  slot="modal-title"><h4 class="my-auto mx-auto form-header">{{'confirmPhoneH1' | localize}}</h4> </template>
        <form 
          class="change-mail-form p-2 m-2" 
          @submit.prevent="checkKodFromPhone" 
          novalidate
        >
          <div class="new-address-description ">{{'confirmPhoneDescription' | localize}} <br> {{phone_mask}}</div>
          <b-button 
            class="my-3 style-button button-profile"
            @click="sendMessageInPhone"
            variant="outline-danger"
            v-if="send_button"
          >
            {{'confirmPhoneSendButton' | localize}}  <div class="style-button-icon"> <b-icon  icon="mailbox" ></b-icon> </div> 
          </b-button>
          <div v-if="!send_button" class="my-5 form-header"> {{'confirmMailSendDescription' | localize}} {{'confirmPhoneFormNumTel' | localize}}</div>
          <div class="new-address-description ">{{'confirmPhoneFormDescription' | localize}}:</div>
          <input 
            v-model="tel_cod_send"
            type="text"
            class="form-control"
          >
          <div 
              class="form-description-error"
              v-if="$v.tel_cod_send.$dirty && !$v.tel_cod_send.required"
          >
              {{'newAddressRequiredInput' | localize}}
          </div> 
          <b-button 
            class="mt-3 mb-5 style-button button-profile"
            variant="outline-danger"
            type="submit"
          >
            {{'confirmPhoneCheckButton' | localize}} <div class="style-button-icon"> <b-icon  icon="mailbox" ></b-icon> </div> 
          </b-button>
          <b-button class="my-auto" @click="change_phone= !change_phone" variant="secondary">{{'confirmMailChangeButton' | localize}} номер</b-button>
        </form>
      

        <ChangePhoneConfirmForm v-if="change_phone" @editData="editData"></ChangePhoneConfirmForm>

<!--      <template slot="modal-cancel" variant="danger"> {{'headerLogout' | localize}} </template>-->

      <template slot="modal-ok" disabled> {{'confirmMailChangeEndButton' | localize}} </template>
    </b-modal> 
  </div>
</template>
<script>
import config from '@/config'
const ROUTE_PREFIX = config.ROUTE_PREFIX

import { mapGetters } from 'vuex'
import { validationMixin } from 'vuelidate'
import { required } from 'vuelidate/lib/validators'
import ChangePhoneConfirmForm from './ChangePhoneConfirmForm'
export default {
  mixins: [validationMixin],
  props: ['modal1_phone_confirm'],
  data: ()=> ({
    tel_cod_send: '',
    user_tel: localStorage.weltum_user_tel,
    phone_mask: '',
    message: '',
    tel_kod_answer: '',
    test: false,

    change_phone: false,
    disabled_button: true,
    send_button: true
  }),
  validations: {
      tel_cod_send: {required},
  },
  components: {ChangePhoneConfirmForm},
  computed: mapGetters(['locale', 'tel_message', 'tel_kod']),
  mounted() {
    this.test = this.modal1_phone_confirm
    this.phoneMask()
  },
  methods: {
    async exit(){
        localStorage.removeItem("weltumLastUrl")
        await this.$store.dispatch('logout') 
        this.$router.push( { path: `${ROUTE_PREFIX}/login` } )
    },
    phoneMask(){
      this.phone_mask = '+38 ' + '(' + this.user_tel[2] + this.user_tel[3] + this.user_tel[4] + ') ' + this.user_tel[5] + this.user_tel[6] + this.user_tel[7] + '-' + this.user_tel[8] + this.user_tel[9] + '-' + this.user_tel[10] + this.user_tel[11]
    },
    async sendMessageInPhone(){
      const MESSAGE = {
        login: localStorage.weltumAuth,
        tel: localStorage.weltum_user_tel
      }
      await this.$store.dispatch('sendSessageToTel', MESSAGE)
        this.message = this.tel_message
        if(this.tel_message === 'OK'){
          this.send_button = false
          setTimeout( ()=> {
            this.send_button = true
          }, 30000)
        }      
    },
    async checkKodFromPhone(){
      if(this.$v.$invalid){
        this.$v.$touch()
      } else {
        const KOD = {
          login: localStorage.weltumAuth,
          tel: localStorage.weltum_user_tel,
          pass_confir: this.tel_cod_send
        }
        await this.$store.dispatch('checkMessageFromPhone', KOD)
        this.tel_kod_answer = this.tel_kod
        if(this.tel_kod_answer === 'Ошибка подтверждения !') {
          this.$messageLocalized("wrongData")
        } else if (this.tel_kod_answer === 'OK') {
          this.$messageLocalized("dataConfirmed")
          this.disabled_button = false   
        }
      }
    },
    editData(mail_changed, new_mail){
      this.change_phone = mail_changed
      this.user_tel = new_mail
      // console.log(mail_changed, new_mail)
      this.phoneMask()
    }
  }
  
}
</script>
